        <section id="introduction" ref="sections">
          <h2>简介</h2>
          <p>老君山（The Laojun
            Mountain），“天下无双圣境，世界第一仙山”。原名<span>景室山</span>，位于十三朝古都<span>洛阳</span>的栾川县县城东南三千米处。八百里<span>伏牛山脉</span>的<span>主峰</span>，玉皇顶海拔2217米。距今已有两千多年人文历史，是道教中历史最长的山脉。
          </p>
          <p>
            春秋时期，被公认为道教始祖的老子李耳到此归隐修炼，使之成为“道源”（道教起源地）和“祖庭”（祖师之庭）。北魏始于其上建老君庙以纪念。唐贞观十一年（637年），唐太宗派尉迟敬德重修景室山铁顶老君庙，并赐名为“老君山”。明万历三十一年（1603年），明神宗诏谕老君山为“天下名山”，并发帑金建殿，成为是历史上唯一被皇封为“天下名山”的中国山脉。
          </p>
          <p>
            老君山景观区6处、有景点179个，<span>太清宫</span>、<span>十方院</span>、<span>灵官殿</span>、淋醋殿、<span>牧羊圈</span>、<span>救苦殿</span>、<span>传经楼</span>、<span>观音殿</span>、<span>三清殿</span>、<span>老君庙</span>等庙宇16处。马鬃岭南侧有三千余亩<span>石林</span>景观对游人开放，被地质学者称为“北国石林”。
          </p>
          <p>
            老君山现为<span>世界地质公园</span>、<span>国家AAAAA级旅游景区</span>、<span>国家地质公园</span>、<span>国家级自然保护区</span>、省级重点文物保护单位、省级<span>风景名胜区</span>，中国<span>北方道教</span>信众拜谒圣地，中原山水文化杰出代表。
          </p>
          <p>2014年，老君山老子铜像被世界基尼斯总部输录为 “大世界基尼斯之最——世界最高的老子铜像”。
            亦是当代仙侠影视剧热门取景地，《<span>诛仙·青云志</span>》《<span>天乩之天帝传说</span>》等皆曾于此取景。</p>
        </section>
        <section id="history" ref="sections">
          <h2>发展历史</h2>
          <p>老君山古号“景室山”，取八百里伏牛山美景集于一室之意，因道家始祖老子归隐修炼于此，后更名为“老君山”，沿袭至今。老君山海拔2217米，是八百里伏牛山主峰，其地质景观为世界一流资源。</p>
          <p>老君山位于洛阳栾川县，历史上的栾川，长期属卢氏县管辖。</p>
          <p>《卢氏县志·碑志》中记载老君山，称“北魏时，为作纪念，在山上建有老君庙。到唐太宗时重修，由大臣尉迟敬德监修”。</p>
          <p>这老君庙为铁椽铁瓦，又称铁顶，明代香火尤为鼎盛。明万历年间，卢氏县令高出作《登景室山赋》，序中也写道：“余至卢氏，闻境有山，巍峨际天，俗传为老子之居，即以老君名之。”可见老子归隐景室山的说法历来盛行。</p>
          <p>
            <span>东周</span>时期，因“守藏室史”<span>李耳</span>到此归隐修炼，被道教尊为<span>太上老君</span>，唐太宗赐名为“老君山”，沿袭至当代。老君山因太上老君李耳在此归隐修炼而得名，具有2000多年的悠久<span>道教文化</span>历史。
          </p>
          <p>
            老子写成《<span>道德经</span>》后，就告别函谷关的关令尹喜，骑着青牛而去，《史记》记载：“莫知其所终”。据文字记载，老子归隐于洛阳景室山，即老君山。因此老子文化和道家文化深深地影响着老君山地区，老君山也成为中原山水文化的杰出代表。老君庙、灵官殿、淋醋殿、道德府等庙宇，古朴凝重，香火炽盛，一直是中国北方各省道教信众的拜谒圣地。
          </p>
          <p><span>北魏</span>时期，为作纪念，在山上建有老君庙。</p>
          <p>贞观十一年（公元637年），唐太宗李世民派尉迟敬德做监工重修了老君庙，并赐御笔亲书的匾额“天下名山”。 </p>
          <p>
            明<span>万历</span>年间，卢氏县令高出作《登景室山赋》，序中也写道：“余至<span>卢氏</span>，闻境有山，巍峨际天，俗传为<span>老子</span>之居，即以<span>老君</span>名之。”
          </p>
          <p>
            1644年李自成攻入北京，崇祯帝自缢殉国，16岁的太子<span>朱慈烺</span>落入李自成之手，随大顺军赴山海关讨伐吴三桂，兵败后潜至栾川，<b>在老君山伪装成道人30多年</b>，被追随而来的遗民暗中尊为皇帝，直到51岁死去。老君山下原有太子墓及碑等，四篇碑文可算铁证。
          </p>
          <p>诗人<span>王心鉴</span>作有《老君山》诗，言到“云岭一万重，秋来此间清。扑面迎翠色，回转别泉声。金顶倾紫气，石林逸仙风。深涧皆静寂，蛱蝶不知名。”</p>
          <p>1997年由国务院批准建立<span>国家级自然保护区</span>，是<span>世界地质公园</span>，<span>国家地质公园</span>。</p>
          <p>2010年9月11日，“老子文化国际论坛”在栾川老君山景区胜利闭幕。来自我国及日本、韩国、比利时等国家的百余名知名研究专家莅会。</p>
          <p>2017年4月25日，河南洛阳老君山举行仙境朝圣节，老君山老子文化苑世界最高老子像前，来自四面八方的数百道士进山朝拜道祖老子，绵延百米场面壮观。</p>
        </section>
        <section id="environment" ref="sections">
          <h2>地理环境</h2>
          <p>
            老君山，位于洛阳市西南方约150公里处，是伏牛山脉的主峰，相传是道教始祖李耳（著名的“老子”）的归隐修炼之地，北魏时在山中建庙纪念，唐贞观年间（公元627-649年）受到皇封，修建“铁顶老君庙”，明万历十九年（公元1591年）颁赐老君山道经诏谕，封为“天下名山”。从洛阳的锦远汽车站（也就是长途汽车站），乘坐开往栾川县城的班车，在“老君山路口”下车后，再换乘当地的摩的，就可以到达景区售票处了。
          </p>
          <p>
            老君山是<span>伏牛山国家地质公园</span>的核心景区之一，记录了十九亿年前造山等地质构造的演变过程，山中具有独特的“滑脱峰林”地貌、壮观的石林景区等景观群，非常震撼心灵。山中树木茂盛，风景如画，处处都能看到山泉从山石缝隙中溢出，在郁郁葱葱的山林中隐藏着十几处瀑布，清澈的激流从数十米高的崖顶跌落下来。雨后的老君山，云雾环绕在各个山峰中，如果你去山顶，还有机会观赏到极美的云海和日出。老君山风景区面积庞大，主要分为居中的老君山主景区、西部的追梦谷景区和东部的寨沟景区。
          </p>
          <h3>气候特点</h3>
          <p>老君山属<span>温带季风气候</span>，年均气温12.4℃，年日照2103小时，无霜期198天，年均降水量872.6毫米。年降水量最高达1386.6毫米，最少403.3毫米。</p>
          <h3>地形地貌</h3>
          <p>洛阳老君山位于河南省西部的洛阳市栾川县城东南三公里处，老君山原貌保存良好，它记录着十九亿年来<span>华北古陆</span>块南缘裂解、离散、增生、聚合、碰撞、造山等构造演化过程。</p>
          <p>
            老君山以中低山夹河谷地貌为主地形起伏大，属华北地层区豫西分区，跨越熊耳山小区和伏牛山小区，出露地层有：太古界太华群，下元古界宽坪群，中元古界长城系熊耳群、蓟县官道口群和栾川群，上元古界青白口系陶湾群、古生界奥陶系二郎坪群、新生界古近系和第四系，岩性岩相变化大；有区域性的深大断裂分布，地质构造复杂。
          </p>
