          <h3>龙吟听泉</h3>
          <p>
            龙吟阁是观景亭，它坐落在老龙窝栈道之上。由于龙吟阁坐落位置独特，所以在这儿听群瀑泉声是游人的一大享受。溪流从<span>珍珠滩</span>留下，到了龙吟阁下，被山岩拘成湍流快速流入深潭，经过了一系列的缓急冲撞之后，留下了大小十几个瀑布，瀑布水声能够在涧谷中的<span>回音壁</span>间回荡。
          </p>
          <img src="../../public/images/scene3.jpg" alt="">
          <h3>中鼎云涌</h3>
          <p>
            <span>云海</span>，是老君山的一大奇观，而中鼎云海为老君山云海之首。云海出现时，放眼望去，云遮千里，雾锁万峰。犹如站在碧波万顷的汪洋大海之中，翠峰隐现似动若静。在上晴下阴的云海出现时，显现出“海到天涯天为岸，山至极顶人为峰”的景观。
          </p>
          <img src="../../public/images/scene4.jpg" alt="">
          <h3>枫林醉秋</h3>
          <p>
            过了<span>老龙窝</span>，是一片<span>油松林</span>，旅游步道穿林而过，给人一种曲经通幽之感。林下便是<span>珍珠滩</span>。圆乎乎、光溜溜的鹅卵石在水光的映衬下，犹如珍珠散盘。清澈的溪水在珍珠间浅吟低唱。与轻烟似的松韵一起荡涤着来自喧嚣都市游人心中的烦恼，为游客憧憬着人生的五彩之梦。
          </p>
          <img src="../../public/images/scene5.png" alt="">
          <h3>舍身崖</h3>
          <p>
            舍身崖是山顶游览过程中的第一个漂亮的自然风光景点。翘首而望，看那古松摩云，危崖欲倾，确实雄伟壮观。这就是老君山一处胜景“舍身崖”，每当日落之时，此处“余辉返照千山色，满峪参差人画中”，瑰丽的晚霞映着诡奇万状的险峰怪石，奇光异景，色彩缤纷，令人赞叹不已。
          </p>
