        <section id="culture" ref="sections">
          <h2>历史文化</h2>
          <h3>道教文化</h3>
          <p>
            老君山有两千多年道教文化历史。传说老子传《道德经》后，告别函谷关的关令尹喜，骑着青牛而去，《史记》记载“莫知其所终”。另有记载称老子归隐于洛阳景室山，即老君山，景室山因老子在此归隐修炼而得名。北魏时在山中建庙纪念老子。唐贞观年间受到皇封，修建“铁顶老君庙”，唐太宗赐名为“老君山”，沿袭至今。明万历十九年（1591）颁赐老君山道经诏谕，封为“天下名山”。
          </p>
          <p>
            老君山有太清宫、十方院、灵官殿、淋醋殿、牧羊圈、救苦殿、传经楼、观音殿、三清殿、老君庙等庙宇16处。其中老君庙、灵官殿、淋醋殿、道德府等庙宇，古朴凝重，香火炽盛，一直是中国北方道教信众的拜谒圣地。这些宫馆从北魏至当代毁毁修修，现存庙宇中顶峰老君庙规模最大。
          </p>
          <p><b>金顶道观群</b></p>
          <p>
            老君山金顶道教建筑群依托山顶建筑，主要包括老君庙、道德府、五母金殿、亮宝台、玉皇顶、钟鼓楼、南天门、朝阳洞、大道院、神道天桥、回廊等，全部采用明清皇家宫殿式建筑形式，特别是金殿、亮宝台、玉皇顶的三座金顶，成为老君山道观群的亮点。
          </p>
          <p><b>太清宫</b></p>
          <p>
            山顶太清宫始建于北魏，历代重修，唐代尉迟敬德曾监工重修，明朝最为鼎盛，铁椽、铁瓦、金碧辉煌，千百年来，一直是豫、陕、皖、鄂等地众香客朝拜的中原道教圣地。东南沿海香客也慕名前来，仅台湾高雄九龙八风进香团每批朝山者就多达百人。
          </p>
          <h3>历史考证</h3>
          <p>据史书记载：“老君山，古号景室，中峰插天，诸峦翠立，去岭巅四十里（离老北街四十里），延 70余里”。因道家始祖老子归隐修炼于此，后更名老君山沿袭至今。</p>
          <p>一、志书典籍佐证</p>
          <p>
            清《卢氏县志》载：“国还归于周昭王时过函谷，犹龙之叹，现身说法，妙用无穷，当其过函谷，令尹请曰：子将隐矣，强为我著书，于是乃著上下篇五千余言。令尹者即尹喜，字文公，道家所称，灵宝天尊是也。公尝仰观乾象，见东方紫气浮关，知有神圣过物色而迎之，果遇老子。今灵邑太初观（即古尹喜），候见老子终处，南宗圣宫，即尹喜之故宅也”。老子写成《道德经》后，就告别了函谷关的关令尹喜，骑着青牛而去了，《史记》记载：“莫知其所终”。据以下文字记载，老子归隐于洛阳景室山，即老君山。
          </p>
          <p>1、《河南通志》卷七载：“老君山在卢氏县东南二百五十里，相传老子修道于此，上有丹灶、丹井。”</p>
          <p>2、《肇城志》卷二十九记载：“老君山在县南二百五十里，南跨鲁山，东接嵩县，周围不知几许。突峰悬崖，隐见云表。昔老子修炼于此上，有老君洞，丹灶、丹井遗迹俱存，人莫能至。传闻陟其顶，则襄汉诸山可以指教。”</p>
          <p>3、《太平寰宇记》：“荆楚之地，卢氏东南近处有老君山，纪念老子李耳。中原老聘是楚人。”（宋太宗太平兴国年间（1976—983）。</p>
          <p>
            4、《老子翼》卷七记载：“义曰:于是者，发句之瑞也；演者，广畅之理也。二篇者指道德二经也。王子年《拾遗记》曰：老君居景室之山，与世人（迹，唯老叟五人，或乘鸣鹤，或着羽衣，共谭天地之数，所撰书经垂十万言。有浮提国献善书二人，乍老乍少，隐形则出影，闻声则藏形。时出金壶器，中有墨汁，状若淳漆，洒木石皆成篆隶科之字，记造化人伦之始。老君所撰经，皆写以玉牒，级以金绳，贮以玉函。及金壶汁尽，二人乃欲呕心沥血以代墨焉。
          </p>
          <p>
            5、《拾遗记》卷三记载：“老聘在周之末，居反景室之山，与世绝迹。惟有黄发老叟五人，或乘鸿鹤，或衣羽毛，而出于顶，瞳子皆方，面色玉洁，手握青筠之杖，与聘共谈天地之数。及聘退迹为柱下史，求天下服道之术，四海名士，莫不争至。五老即五方之精也。浮提之国，献神通善书二人，乍老乍少，隐形则出影，闻声则藏行。出肘间金壶四寸,上有五龙之检，封以青泥。壶中有墨汁如淳漆，洒地及石，皆成篆隶科之字。记造化人伦之始，佐老子撰《道德经》垂十万言。写以玉牒，编以金绳，贮以玉函。昼夜精勤，行劳神倦。及金壶汁尽，二人呕心沥血，以代墨焉。递钻脑骨取髓，代为膏烛。及髓血皆竭，探懐中玉管，中有丹药之屑，以涂其身，骨乃如故。老子曰：“更除其繁，存五千言”及至经成工毕，二人亦不知所往。”
          </p>
          <p>
            6、明朝万历皇帝朱翊钧仰慕老子修道养生而获的长寿之术，故信道教，特立“万历皇帝老君山诏谕牌”，虔心祈祷：“联躬康泰，无疆福寿，民安国泰，天下太平。”明神宗万历二十三年（1595），朝廷遣中使，发帑金，建栾川老君山太清官宫殿。继之于神宗万历三十一年（1603），朝廷印造《道大藏经》颁发老君山。
          </p>
          <p>
            7、明嘉靖《南阳府志?校注》卷二载：“老君山，在内乡县西北250里处，突峰悬崖，隐现云表，世传老子学道于此，药灶、丹炉遗迹俱存，故名”，此庙原有铁瓦盖顶，殿堂两座，内奉老君塑像，旁边铸造铜牛一头，过去香客经年不绝，有“南祖师，北老君”之说——（南阳民族宗教志180页。）
          </p>
          <p>8、清《内乡县志》卷一记载：“老君山在县北250里处，突峰悬崖，隐现云表，世传老子学道于此，药灶、丹炉遗迹俱存”。</p>
          <p>9、清乾隆年间《卢氏县志》记载：“老君山……老子讲经修炼于此。有老君殿、石柱、铁瓦、丹炉、丹井犹存，明代曾贮藏经八柜，至明末毁蚀无余，顺治间王调元重修。”</p>
          <p>
            10、清《卢氏县志?艺文卷》《敕建老君山金顶太清观记》载：“老君修炼于景室山，在治东南200里许，老君仙山是也，盘据中南之首，太和嵩少为副，绝顶巍峨际天。昔（时）万历二十三年春遣中使，发帑金敕建宫殿。颁赐道经藏贮于中。天启三年秋（1623年），邑处士甯（宁）功明暨门朋志友捐金帛、寿梓、雕刻祖师佑圣真君，周陶（应为桃花）元帅神像，配享老君烟火。
          </p>
          <p>11、清《卢氏县志?山川卷》载：“老君山在城南200里栾川镇（原属卢氏辖治），东鸦关岭，西帽盔山，南要儿关，北临伊河。高40里，南望武当，北视嵩岳，东见龙门，西府熊耳。</p>
          <p>12、《卢氏县志?碑志》载：“北魏时，为作纪念，在山建有老君庙。到唐太宗时重修，由大臣尉迟敬德监修”。</p>
          <p>13、清《卢氏县志?艺文志》记：太清观在老君山，明万历二十三年（1595年）春朝廷遣中使，发帑金，敕建宫殿。朝阳宫、太和宫俱在老君山下。</p>
          <p>14、《栾川县志》在《大事记》《卷五文化》中也都有详细记载</p>
          <p>二、文物遗迹印证</p>
          <p>
            1、明铁钟：高1.3米，口径1.65米，平均厚3公分，重500余公斤。明万历十九年（1591年）三月吉日造。钟甬为二龙纹饰，肩部四周有四个圆孔，肩下周围铸：“皇帝万岁，太子千秋，风调雨顺，国泰民安”铭文，铁钟内容因风雨锈蚀，难辨认，大都是捐资者名录，共1050个字，有洛阳、宜阳、嵩县、卢氏、河南府等，还有主持道人刘真环等。
          </p>
          <p>
            2、明碑刻：大明万历三十一年四月初八日立，高1.1米，宽0.51米。碑文：谕老君山太清观住持及道众人等：朕发诚心，印造《道大藏经》，颁赐在京及天下名山供奉。经道护敕，已谕由尔住持及道众等，虔诰供安，朝夕礼颂，保安朕躬康泰，宫台睿承千秋，无疆寿福，民安国泰，天下太平，四方同归，清净善教。朕成恭已，无为之善道道经。厥倚香惜薪司左司副晷颁发，斋戒前去奉安，各宜仰体知悉。钦哉！故谕。大明万历三十一年四月初八
          </p>
          <p>3、放光金殿高1.55米，边宽0.65米。清乾隆十二年（1747年）十二年三月十五日吉造（未修复）门额铸“放光金殿“，上联：安南辰定北斗开天辟地。下联残，经笔者查找应为：分阴阳判水火炼海烧山。</p>
          <p>4、铁铸神像明嘉靖十八年（1539年）铁铸道像18尊，已修复12尊。</p>
          <p>5、明万历二十三年（1595年）敕建金项太清观使用的铁椽铁瓦400余件。有铭文。</p>
          <p>6、老君骑牛铜像（鎏金）。经到法院看审判杨道的原始档案记录，为洪钧老祖坐骑。</p>
          <p>7、救苦殿碑刻，阳面“永存千古”，阴面碑文：“夫山胡以景室也。盖发于秦岭，结于九皋，缠绵数千里，而钟灵毓秀于此者也”。</p>
          <p>三、诗词凭证</p>
          <p>1、唐?薛能《伏牛山》诗：虎蹲峰状屈伏牛，落日连村妇望秋。</p>
          <p>2、明?李衮咏老君山：青牛老子幽关去，遗庙苍山万仞巅。</p>
          <p>3、明代诗人高出在《登景室山》诗中写道：峭倚中天矗，高临五岳尊，函谷著高论，幽室炼丹砂。</p>
          <p>4、清代诗人李珩在游记中写道：“栾川老君山号称景室，石柱铁瓦，铜铸老子骑青牛像。东瞻二室，南望武当，四面烟岚，一瞬无际，诚域内之盛观。</p>
          <p>四、传说及民俗等旁证</p>
          <p>1、从史料典籍及文物遗迹所印证，都确定老子从虢国灵宝，经卢氏到景室归隐修炼。全国道教名山祭奉的有真武师祖、张天师等，惟独老君山二千余年祀奉道祖太上老君。</p>
          <p>2、据考证，北魏始建庙于景室山，隋朝确认景室山为老子归隐圣地，山因圣人名，更名老君山沿袭至今。</p>
          <p>3、唐代太宗李世民尊老子为“帝室先系“，唐高宗李治封老子为”太上玄元皇帝“，唐玄宗李隆基亲自为《道德经》作注，并把《道德经》作为贡举考试的经典。派开国元勋尉迟敬德监修老君山道观。</p>
          <p>
            4、明代万历帝朱翊钧一生多病，仰慕老子修道养生，寄托于老君山道教神灵中，有诏谕碑明证。明代发帑金，敕建道观，颁《道大藏经》等，明代庙宇宏伟辉煌，居历代之最，香火达高峰。观光朝圣盛况空前。由此，足以证明明太子朱慈烺托迹黄道，在老君山隐迹35年之久的渊源。康熙十八年（1679年）十月，明太子病逝于栾川，厝葬于“仙坟墓园”中，翌年三月初六，迁葬于朝阳宫后岭新建陵墓，称“太子坟”，王冲如撰文万仞宫墙，有门难进。
          </p>
          <p>
            5、民俗及地名等：老君李耳到景室山归隐后，沿路留下了与老子有关的传说、地名、民俗等，抱犊寨传说是老子牧童成仙而化。栾川附近的老君洞、老君堂及老君钻、老君灶等，都与老君有关。从卢氏到栾川一线敬祖上坟日为老子出生的二月十五日，并家家户户敬老君爷，不论烧砖瓦、开矿等都把老君作为崇拜的道尊。
          </p>
          <p>
            6、四月八老君庙会的由来，民间传说是为纪念老君到景室山之日，也有一种说法是老君升仙之日。但目前唯一的证明就是明神宗万历1603年四月初八命名“天下名山”的镌诏谕旨立石。止今409年历史，属于河南省“非物质文化遗产”。
          </p>
        </section>
      </div>
