<template>

  <div class="body">
    <div class="container" id="container">
      <div class="content">
        <div class="form">
          <input type="text" v-model="name" placeholder="署名">
          <input type="text" v-model="title" placeholder="标题">
          <textarea v-model="message" placeholder="留言内容"></textarea>
          <button @click="postMessage">发表</button>
        </div>

        <div class="messages">
          <div class="message" v-for="msg in messages" :key="msg.id">
            <span class="title">{{ msg.title }}</span>
            <span class="time">{{ msg.date }}</span>
            <span class="author">{{ msg.author }}</span>
            <span class="message-body">{{ msg.content }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
export default {
  data() {
    return {
      name: '',
      title: '',
      message: '',
      messages: [
        {
          id: 1,
          title: '天气',
          author: '小黑',
          content: '今天天气真是不错啊!!!',
          date: '2024-05-16 14:38:15'
        },
        {
          id: 2,
          title: '天气',
          author: '小黑',
          content: '今天天气真是不错啊!!!',
          date: '2024-05-16 14:38:15'
        }
      ]
    };
  },
  methods: {
    postMessage() {
      if (this.name && this.title && this.message) {
        this.messages.push({
          id: this.messages.length + 1,
          author: this.name,
          content: this.message,
          date: new Date().toLocaleString()
        });
        this.name = '';
        this.title = '';
        this.message = '';
      } else {
        alert('请填写所有字段');
      }
    }
  }
};
</script>

<style scoped>

.body {
  width: 100%;
  background-color: rgba(150, 166, 185, 1);
}

.content{
  display:flex;
  flex-direction: column;
  align-items:center;
}

.form {
  width: 979px;
  height: 516px;
  border-radius: 16px;
  padding-top:20px;
  background-color: rgba(37, 84, 136, 0.7);
  text-align: center;
  margin-top:16px;
  display:flex;
  flex-direction: column;
  align-items:center;
  position:relative;
  margin-bottom: 14px;
}

.form input {
  width: 199px;
  height: 63px;
  border-radius: 32px;
  padding-left: 1em;
  border: 1px solid rgba(102, 111, 124, 1);
  background-color: rgba(37, 84, 136, 0.5);
  color: white;
  margin:10px auto;
  font-size:24px;
  outline: none;
}

.form input:focus ,.form textarea:focus,.form button:hover{
    border: 1px solid white; /* 自定义focus时的边框样式 */
    outline: none; /* 确保focus时没有默认的outline */
    background-color: rgba(37, 84, 136, 1);
}


.form textarea {
  width: 498px;
  height: 293px;
  background-color: rgba(37, 84, 136, 0.35);
  border-radius: 16px;
  color: white;
  padding: 1em 1em;
  position: absolute;
  bottom: 24px;
  font-size:20px;
  outline: none;
}

.form input::placeholder,
.form textarea::placeholder {
  color: rgba(255, 255, 255, .5);
}

.form button {
  width: 130px;
  height: 130px;
  background-color: rgba(37, 84, 136, 0.5);
  border-radius: 99px;
  border: 1px solid rgba(102, 111, 124, 1);
  color: white;
  position:relative;
  bottom: 0;
  right: 0;
  position:absolute;
  bottom: 24px;
  right:44px;
  font-size:28px;

}

#container {
  background-image: url('../../public/images/boardBg.png');
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.message {
  width: 860px;
  height: 158px;
  background-color: rgba(37, 84, 136, 0.5);
  border-radius: 16px;
  margin: 13px auto;
  padding: 1em 1em;
  color: white;
  position:relative;
  font-size:24px;
}

.message .title {
  font-size: 32px;
  top:1em;
  left:1em;
}

.message .time {
  position: absolute;
  top: 1em;
  right: 1em;
}

.message .author{
  position: absolute;
  bottom: 0.5em;
  right: 1em;
}

.message .message-body{
  position:absolute;
  bottom:1em;
  left:1em;
}
</style>